import React, { useRef, useEffect, useState } from "react";
import Hls from "hls.js";
import { useSelector, useDispatch } from "react-redux";
import {
  hidePopupVideoPlayer,
  setHidePopupVideoPlayerList,
  setEndedVideoPlayerList,
  updatePlaytime,
  updatePlaytimeLastWeek,
  updatePlaytimeLastWeekSelected,
} from "../redux/exerciseVideos";
import {
  completeVideoPlayPercentage,
  minimumVideoPlayPercentage,
  updateFrequency,
} from "../constants/defaultValues";

const VideoPlayerListByteArk = ({
  url,
  day_number,
  video_number,
  selectedVDO,
  lastWeekVDO_click,
  lastWeekVDOAll,
  lastWeekStart,
  selectExerciseVideoLastWeek,
}) => {
  const dispatch = useDispatch();
  const hidePopUpVideoPlayer = useSelector(({ exerciseVideos }) =>
    exerciseVideos ? exerciseVideos.hidePopUpVideoPlayer : ""
  );
  const exerciseVideo = useSelector(({ exerciseVideos }) =>
    exerciseVideos ? exerciseVideos.exerciseVideo : ""
  );
  const all_exercise_activity = useSelector(({ exerciseVideos }) =>
    exerciseVideos ? exerciseVideos.all_exercise_activity : ""
  );
  const exerciseVideoLastWeek = useSelector(({ exerciseVideos }) =>
    exerciseVideos ? exerciseVideos.exerciseVideoLastWeek : ""
  );
  const endedVideoPlayerList = useSelector(({ exerciseVideos }) =>
    exerciseVideos ? exerciseVideos.endedVideoPlayerList : ""
  );
  const user = useSelector(({ authUser }) => (authUser ? authUser.user : ""));
  const videoRef = useRef(null);
  const [videoEnded, setVideoEnded] = useState(false); // à¹à¸à¸´à¹à¸¡ state à¸ªà¸³à¸«à¸£à¸±à¸à¸à¸£à¸§à¸à¸ªà¸­à¸à¸§à¹à¸²à¸§à¸µà¸à¸µà¹à¸­à¸à¸¹à¸à¸à¸¹à¸à¸à¸«à¸£à¸·à¸­à¹à¸¡à¹
  const [videoCurrDuration, setVideoCurrDuration] = useState(0); // à¹à¸à¸´à¹à¸¡ state à¸ªà¸³à¸«à¸£à¸±à¸à¹à¸à¹à¸à¸£à¸°à¸¢à¸°à¹à¸§à¸¥à¸²à¸à¸µà¹à¹à¸¥à¹à¸à¹à¸à¸à¸­à¸à¸§à¸µà¸à¸µà¹à¸­
  const [videoDuration, setVideoDuration] = useState(0); // à¹à¸à¸´à¹à¸¡ state à¸ªà¸³à¸«à¸£à¸±à¸à¹à¸à¹à¸à¸à¸§à¸²à¸¡à¸¢à¸²à¸§à¸à¸­à¸à¸§à¸µà¸à¸µà¹à¸­
  const [prevPlayTime, setPrevPlayTime] = useState(0);

  useEffect(() => {
    addEventListenerVideo();
    setVideoCurrDuration(0);
    dispatch(setEndedVideoPlayerList(false));
  }, [selectedVDO]);

  const addEventListenerVideo = () => {
    const video = videoRef.current;

    if (video && selectedVDO) {
      const filterVideoAutoPlayEachDay = exerciseVideo
        .filter((innerArray) =>
          innerArray.some((obj) =>
            obj.name.toLowerCase().includes(selectedVDO.name.toLowerCase())
          )
        )
        .flat();
      if (Hls.isSupported()) {
        const hls = new Hls();
        hls.loadSource(selectedVDO.url); // à¹à¸à¹ URL à¸à¸µà¹à¸à¸¹à¸à¸ªà¹à¸à¹à¸à¹à¸²à¸¡à¸²à¹à¸ props
        hls.attachMedia(video);
        hls.on(Hls.Events.MANIFEST_PARSED, () => {
          video.play();
        });
      } else if (video.canPlayType("application/vnd.apple.mpegurl")) {
        video.src = selectedVDO.url; // à¹à¸à¹ URL à¸à¸µà¹à¸à¸¹à¸à¸ªà¹à¸à¹à¸à¹à¸²à¸¡à¸²à¹à¸ props
        video.addEventListener("canplay", () => {
          video.play();
        });
      }

      video.addEventListener("ended", () => {
        setVideoEnded(true); // à¸à¸³à¸«à¸à¸à¸§à¹à¸²à¸§à¸µà¸à¸µà¹à¸­à¸à¸¹à¸à¸à¸¹à¸à¸
        dispatch(setEndedVideoPlayerList(true));
      });

      video.addEventListener("loadedmetadata", () => {
        const videoDuration = video.duration; // à¸à¸§à¸²à¸¡à¸¢à¸²à¸§à¸à¸­à¸à¸§à¸µà¸à¸µà¹à¸­ (à¹à¸à¸§à¸´à¸à¸²à¸à¸µ)
        setVideoDuration(videoDuration);
      });

      video.addEventListener("timeupdate", () => {
        setVideoCurrDuration(video.currentTime); // à¸­à¸±à¸à¹à¸à¸à¸£à¸°à¸¢à¸°à¹à¸§à¸¥à¸²à¸à¸µà¹à¸à¸¥à¸´à¸à¸à¸¹à¸à¹à¸¥à¹à¸à¹à¸
      });
    }
  };

  useEffect(() => {
    if (videoEnded) {
      //à¸ªà¹à¹à¸à¹à¸à¸à¸­à¸à¸«à¸à¹à¸² videoList2 à¸§à¹à¸²à¸§à¸µà¸à¸µà¹à¸­à¹à¸¥à¹à¸à¸à¸
      dispatch(setEndedVideoPlayerList(true));
    }
  }, [videoEnded]);

  useEffect(() => {
    //console.log("videoDuration :", videoDuration);
  }, [videoDuration]);

  useEffect(() => {
    //à¹à¸à¹à¸à¸à¸¥à¸´à¸à¸à¸­à¸ 99.9% à¹à¸à¸à¸à¸²à¸£à¹à¸à¹à¸à¸§à¸µà¸à¸µà¹à¸­à¸à¸
    if (videoCurrDuration / videoDuration >= 0.999) {
      setVideoEnded(true);
    }

    //à¸à¸³à¸à¸²à¸£à¸«à¸à¹à¸§à¸à¹à¸§à¸¥à¸²à¸à¸²à¸¡ updateFrequency à¹à¸à¸·à¹à¸­à¸¢à¸´à¸ updatePlayTime
    const diffTime = Math.abs(videoCurrDuration - prevPlayTime);
    if (diffTime < updateFrequency) {
      return;
    }
    setPrevPlayTime(videoCurrDuration);

    //à¹à¸à¹à¸à¸§à¹à¸²à¸à¹à¸²à¸à¸¹à¸§à¸µà¸à¸µà¹à¸­à¸¢à¸±à¸à¹à¸¡à¹à¸à¸¶à¸ minimumVideoPlayPercentage à¹à¸¡à¹à¸à¹à¸­à¸ updatePlayTime
    //à¹à¸à¹à¸à¸§à¹à¸²à¸à¹à¸²à¹à¸à¸¢à¸à¸¹à¸à¸¥à¸´à¸à¸à¸±à¹à¸à¸à¸à¹à¸¥à¹à¸§ à¹à¸¡à¹à¸à¹à¸­à¸ updatePlayTime
    if (
      videoCurrDuration / videoDuration < minimumVideoPlayPercentage ||
      selectedVDO.play_time / selectedVDO.duration >=
        completeVideoPlayPercentage
    ) {
      return;
    }

    updatePlayTime();
  }, [videoCurrDuration]);

  const updatePlayTime = () => {
    if (lastWeekVDO_click === "show") {
      if (!lastWeekVDOAll) {
        //updatePlayTime à¸à¸­à¸à¸à¸¹à¹à¹à¸à¹à¸«à¸¡à¸à¸­à¸²à¸¢à¸¸à¸à¸¹à¸¢à¹à¸­à¸à¸«à¸¥à¸±à¸
        const tempExerciseVideoLastWeek = [...exerciseVideoLastWeek];
        tempExerciseVideoLastWeek[day_number][video_number] = {
          ...tempExerciseVideoLastWeek[day_number][video_number],
          play_time: videoDuration,
          duration: videoDuration,
        };

        dispatch(
          updatePlaytimeLastWeek(
            user.user_id,
            user.start_date,
            user.expire_date,
            day_number,
            video_number,
            videoDuration,
            videoDuration,
            tempExerciseVideoLastWeek
          )
        );
      } else {
        //updatePlayTime à¸à¸­à¸à¸à¸¹à¹à¹à¸à¹à¸à¹à¸­à¸­à¸²à¸¢à¸¸à¸à¸¹à¸¢à¹à¸­à¸à¸«à¸¥à¸±à¸
        const tempExerciseVideoLastWeekSelect = [
          ...selectExerciseVideoLastWeek,
        ];
        const tempExerciseVideoLastWeekAll = [...all_exercise_activity];
        tempExerciseVideoLastWeekSelect[day_number][video_number] = {
          ...tempExerciseVideoLastWeekSelect[day_number][video_number],
          play_time: videoDuration,
          duration: videoDuration,
        };
        tempExerciseVideoLastWeekAll[lastWeekStart - 1].activities =
          JSON.stringify(tempExerciseVideoLastWeekSelect);

        dispatch(
          updatePlaytimeLastWeekSelected(
            user.user_id,
            user.start_date,
            user.expire_date,
            day_number,
            video_number,
            videoDuration,
            videoDuration,
            tempExerciseVideoLastWeekAll,
            lastWeekStart
          )
        );
      }
    } else {
      //updatePlayTime à¸à¸­à¸à¸à¸¹à¹à¹à¸à¹à¸à¹à¸­à¸­à¸²à¸¢à¸¸à¸à¸¹à¸à¸¥à¸´à¸à¸à¸±à¸à¸à¸¸à¸à¸±à¸
      const tempExerciseVideo = [...exerciseVideo];
      tempExerciseVideo[day_number][video_number] = {
        ...tempExerciseVideo[day_number][video_number],
        play_time: videoDuration,
        duration: videoDuration,
      };

      dispatch(
        updatePlaytime(
          user.user_id,
          user.start_date,
          user.expire_date,
          day_number,
          video_number,
          videoDuration,
          videoDuration,
          tempExerciseVideo
        )
      );
    }
  };

  const handleVideoClose = () => {
    const video = videoRef.current;
    if (video) {
      video.pause();
      // à¹à¸¥à¸°à¸­à¸·à¹à¸ à¹ à¸à¸µà¹à¸à¸¸à¸à¸à¹à¸­à¸à¸à¸²à¸£à¹à¸«à¹à¹à¸à¸´à¸à¸à¸¶à¹à¸à¹à¸¡à¸·à¹à¸­à¸à¸´à¸à¸§à¸µà¸à¸µà¹à¸­
    }

    //à¸ªà¸±à¹à¸ set à¸à¸±à¸§à¹à¸à¸£à¹à¸ redux à¹à¸¥à¸°à¹à¸«à¹à¸«à¸à¹à¸² videoList à¹à¸à¹à¸à¹à¸µà¸à¸à¸²à¸à¸à¸±à¸§à¹à¸à¸£à¸à¸±à¹à¸à¹à¸à¸·à¹à¸­à¸à¹à¸­à¸ popup
    dispatch(setHidePopupVideoPlayerList(true));
  };

  return (
    <div>
      <video id="videoPlayerList" ref={videoRef} controls />

      <img
        alt=""
        src="../assets/img/thumb/close.png"
        className="close"
        onClick={handleVideoClose}
      ></img>
    </div>
  );
};

export default VideoPlayerListByteArk;
