import React, { Component } from "react";

class GPSTracker2 extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            distance: 0, // à¸£à¸°à¸¢à¸°à¸à¸²à¸à¸à¸µà¹à¸à¸¹à¹à¹à¸à¹à¸§à¸´à¹à¸
            positions: [], // à¸à¸³à¹à¸«à¸à¹à¸à¸à¸µà¹à¸à¸±à¸à¸à¸¶à¸à¹à¸§à¹
            tracking: false, // à¸ªà¸à¸²à¸à¸°à¸à¸²à¸£à¸à¸´à¸à¸à¸²à¸¡ GPS
        };
    }

    // ...

    // à¹à¸à¸´à¹à¸¡à¹à¸¡à¸à¸­à¸à¸ªà¸³à¸«à¸£à¸±à¸à¹à¸£à¸´à¹à¸¡à¸«à¸£à¸·à¸­à¸«à¸¢à¸¸à¸à¸à¸´à¸à¸à¸²à¸¡ GPS
    toggleTracking = () => {
        if (this.state.tracking) {
            // à¸«à¸¢à¸¸à¸à¸à¸´à¸à¸à¸²à¸¡ GPS
            navigator.geolocation.clearWatch(this.watchId);
            this.setState({ tracking: false });
        } else {
            // à¹à¸£à¸´à¹à¸¡à¸à¸´à¸à¸à¸²à¸¡ GPS à¹à¸«à¸¡à¹
            this.watchId = navigator.geolocation.watchPosition(
                this.handlePositionChange,
                this.handlePositionError
            );
            this.setState({ tracking: true });
        }
    };

    handlePositionChange = (position) => {
        const { positions } = this.state;

        // à¸à¸³à¸à¸§à¸à¸£à¸°à¸¢à¸°à¸à¸²à¸à¹à¸¥à¸°à¹à¸à¸´à¹à¸¡à¸à¸³à¹à¸«à¸à¹à¸à¹à¸«à¸¡à¹à¸¥à¸à¹à¸ state
        if (positions.length > 0) {
            const lastPosition = positions[positions.length - 1];
            const distanceIncrement = this.calculateDistance(
                lastPosition.coords.latitude,
                lastPosition.coords.longitude,
                position.coords.latitude,
                position.coords.longitude
            );

            this.setState((prevState) => ({
                distance: prevState.distance + distanceIncrement,
                positions: [...prevState.positions, position],
            }));
        } else {
            this.setState({ positions: [position] });
        }
    };

    handlePositionError = (error) => {
        console.error("Error getting position:", error);
    };

    calculateDistance = (lat1, lon1, lat2, lon2) => {
        // à¸à¸³à¸à¸§à¸à¸£à¸°à¸¢à¸°à¸à¸²à¸à¸£à¸°à¸«à¸§à¹à¸²à¸à¸à¸¸à¸à¸ªà¸­à¸à¸à¸¸à¸à¸à¹à¸§à¸¢ Haversine formula
        // (à¹à¸à¹à¸à¸à¸³à¸à¸§à¸à¸­à¸²à¸à¸¡à¸µà¸à¸§à¸²à¸¡à¸à¸±à¸à¸à¹à¸­à¸à¸à¸§à¹à¸²à¸à¸µà¹)
        const R = 6371; // à¸£à¸±à¸¨à¸¡à¸µà¸à¸­à¸à¹à¸¥à¸à¹à¸à¸«à¸à¹à¸§à¸¢à¸à¸´à¹à¸¥à¹à¸¡à¸à¸£
        const dLat = (lat2 - lat1) * (Math.PI / 180);
        const dLon = (lon2 - lon1) * (Math.PI / 180);
        const a =
            Math.sin(dLat / 2) * Math.sin(dLat / 2) +
            Math.cos(lat1 * (Math.PI / 180)) *
            Math.cos(lat2 * (Math.PI / 180)) *
            Math.sin(dLon / 2) *
            Math.sin(dLon / 2);
        const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
        const distance = R * c;

        return distance;
    };

    render() {
        const { distance, tracking } = this.state;

        return (
            <div>
                <p>à¸£à¸°à¸¢à¸°à¸à¸²à¸à¸à¸µà¹à¸§à¸´à¹à¸à¹à¸à¹: {distance.toFixed(2)} km</p>

                {/* à¹à¸à¸´à¹à¸¡à¸à¸¸à¹à¸¡à¹à¸£à¸´à¹à¸¡à¸à¸±à¸à¸£à¸°à¸¢à¸°à¸à¸²à¸à¸«à¸£à¸·à¸­à¸«à¸¢à¸¸à¸à¸à¸±à¸à¸£à¸°à¸¢à¸°à¸à¸²à¸ */}
                <button onClick={this.toggleTracking}>
                    {tracking ? "à¸«à¸¢à¸¸à¸à¸à¸±à¸à¸£à¸°à¸¢à¸°à¸à¸²à¸" : "à¹à¸£à¸´à¹à¸¡à¸à¸±à¸à¸£à¸°à¸¢à¸°à¸à¸²à¸"}
                </button>

                {/* à¹à¸ªà¸à¸à¸à¸³à¹à¸«à¸à¹à¸à¸à¸µà¹à¸à¸±à¸à¸à¸¶à¸à¹à¸§à¹ */}
                <ul>
                    {this.state.positions.map((position, index) => (
                        <li key={index}>
                            Lat: {position.coords.latitude}, Long: {position.coords.longitude}
                        </li>
                    ))}
                </ul>
            </div>
        );
    }
}

export default GPSTracker2;
