import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';


class GPSPermission extends Component {
    constructor(props) {
        super(props);
        this.state = {
            latitude: null,
            longitude: null,
            error: null,
            debugSuccess: 0,
            debugFail: 0,
            debugGeo: 0
        };
    }

    componentDidMount() {
        // à¸à¸£à¸§à¸à¸ªà¸­à¸à¸§à¹à¸² Geolocation API à¸¡à¸µà¸à¸²à¸£à¸ªà¸à¸±à¸à¸ªà¸à¸¸à¸à¹à¸à¹à¸à¸£à¸²à¸§à¹à¹à¸à¸­à¸£à¹à¸«à¸£à¸·à¸­à¹à¸¡à¹
        if (navigator.geolocation) {
            this.setState({ debugGeo: 1 });
            // à¸à¸­à¸­à¸à¸¸à¸à¸²à¸à¹à¸«à¹à¹à¸à¹à¸²à¸à¸¶à¸à¸à¸³à¹à¸«à¸à¹à¸à¸à¸²à¸à¸ à¸¹à¸¡à¸´à¸¨à¸²à¸ªà¸à¸£à¹
            navigator.geolocation.getCurrentPosition(
                this.handleSuccess,
                this.handleError,
                { maximumAge: 60000, timeout: 5000, enableHighAccuracy: true }
            );
            this.setState({ debugGeo: 2 });
        } else {
            this.setState({ debugGeo: 3 });
            this.setState({ error: 'à¹à¸¡à¹à¸ªà¸à¸±à¸à¸ªà¸à¸¸à¸ Geolocation API' });
        }
    }

    handleSuccess = (position) => {
        this.setState({ debugSuccess: 1 });
        const { latitude, longitude } = position.coords;
        this.setState({ debugSuccess: 2 });
        this.setState({ latitude, longitude });
        this.setState({ debugSuccess: 3 });
        this.props.history.push('/test_gps_success');

    };

    handleError = (error) => {
        this.setState({ debugFail: 1 });
        this.setState({ error: 'à¹à¸¡à¹à¸ªà¸²à¸¡à¸²à¸£à¸à¹à¸à¹à¸²à¸à¸¶à¸à¸à¸³à¹à¸«à¸à¹à¸à¸à¸²à¸à¸ à¸¹à¸¡à¸´à¸¨à¸²à¸ªà¸à¸£à¹' });
    };

    render() {
        const { latitude, longitude, error, debugSuccess, debugFail, debugGeo } = this.state;

        return (
            <div>
                {error ? (
                    <p>{error}</p>
                ) : (
                    <div>
                        <p>debugSuccess: {debugSuccess}</p>
                        <p>debugFail: {debugFail}</p>
                        <p>debugGeo: {debugGeo}</p>
                        <p>Latitude: {latitude}</p>
                        <p>Longitude: {longitude}</p>
                    </div>
                )}
            </div>
        );
    }
}

export default withRouter(GPSPermission);
